<script>
import Blog from "../Blog.vue";
export default {
  components: { Blog },
  data() {
    return {
      rect: null,
      time: "2019-4-26",
      title: "动画和过渡事件",
      items: [
        {
          title: "动画事件",
          target: "#animate"
        },
        { divider: true },
        {
          title: "使用js控制元素动画",
          target: "#js_animate"
        },
        { divider: true },
        {
          title: "过渡事件",
          target: "#transition"
        },
        { divider: true },
      ],
      pres: 
[
`
    const box = document.getElementById('box');
    let count = 1;

    // 动画开始执行
    box.addEventListener('animationstart', (e) => {
        console.log(e)
    })

    // 一次动画执行完了会触发此事件，最后一次结束不会触发
    box.addEventListener('animationiteration', () => { 
        console.log(count);
        count++;
        if(count === 2) {
            box.style.animationName = 'moveY';
            box.style.animationDuration = '3s';
        } else if(count === 3) {
            box.style.animationName = 'moveX';
            box.style.animationDuration = '3s';
            box.style.animationIterationCount = '1';
        }
    });

    // 动画结束执行
    box.addEventListener('animationend', (e) => {
        console.log(e);
    })

    document.getElementById('btn').addEventListener('click', function(e) {
        box.style.animationName = 'moveX';
        box.style.animationDuration = '3s';
        box.style.animationIterationCount = '3';
        this.style.display = 'none';
    })
`,
`
    <div id="box"></div>
    <button type="button" id="btn">开始</button>
`,
`
    @keyframes moveX {
        0% { transform: translateX(0)}
        100% { transform: translateX(300px)}
    }
    @keyframes moveY {
        0% { transform: translateY(0)}
        100% { transform: translateY(300px)}
    }
    #box{
        width: 100px;
        height: 100px;
        background-color: aquamarine;
    }
`,
`
    /**
     * keyframe有如下几种声明方式
     * 指定某个属性动画开始和结束的css
     * const kf = [ 
     *      { backgroundColor: "antiquewhite" }, 
     *      { backgroundColor: "brown" }
     * ];
     * 指定偏移值
     * const kf = [
     *      { backgroundColor: "antiquewhite" },
     *      { backgroundColor: "brown", offset: 0.7 },
     *      { backgroundColor: "red" }
     * ];
     * 指定属性的变化函数
     * const kf = [
     *      { left: '0px', easing: 'ease-in' },
     *      { left: '300px', easing: 'ease-out' }
     * ]
    */
    
    const kf = {
        backgroundColor: ['antiquewhite', 'brown'],
        left: ['0px', '300px'],
        easing: [ 'ease-in', 'ease-out' ]
    }
    // 直接传入动画执行时间即可执行动画
    // const am = box.animate(kf, 3000); 

    // 你可以传入对象，提供更加细粒度的动画控制
    const am = box.animate(kf, {
        id: 'animate',
        delay: 1000, // 延迟
        duration: 2000, // 动画执行时间
        iterations: Infinity,
        direction: 'alternate',
        /**
         * normal 从头开始
         * reverse 反向开始
         * alternate 从头开始，动画结束然后反向执行
         * alternate-reverse 反向开始，动画结束然后反向执行
        */
        easing: 'ease-in-out',
        /**
         * linear 线性
         * ease-in 由慢到快
         * ease-out 由快到慢
         * ease-in-out 由慢到快再到慢
        */
    });
`,
`
    <div id="box"></div>
`,
`
    #box{
        width: 100px;
        height: 100px;
        background-color: antiquewhite;
        position: relative;
        left: 0;
    }
`,
`
    const box = document.getElementById('box');
    box.addEventListener('transitionrun', e => {
        box.innerText = "动画开始执行";
    })
    box.addEventListener('transitionend', e => {
        box.innerText = "动画执行结束";
    })
`,
`
    <div id="box"></div>
`,
`
    #box{
        background-color: azure;
        width: 100px;
        height: 100px;
        transition: all 2s;
        padding: 10px;
        box-sizing: border-box;
        font-size: 12px;
        color: gray;
    }
    #box:hover{
        background-color: brown;
        width: 200px;
        height: 200px;
    }
`
]
    };
  },
    mounted() {
        const box = document.getElementById('box');
        const box1 = document.getElementById('box1');
        let count = 1;
        // 一次动画执行完了会触发此事件，最后一次结束不会触发
        box.addEventListener('animationiteration', () => {
            count++;
            if(count === 2) {
                box.style.animationName = 'moveY';
                box.style.animationDuration = '3s';
            } else if(count === 3) {
                box.style.animationName = 'moveX';
                box.style.animationDuration = '3s';
                box.style.animationIterationCount = '1';
            }
        });

        this.$refs.btn.$el.addEventListener('click', function(e) {
            box.style.animationName = 'moveX';
            box.style.animationDuration = '3s';
            box.style.animationIterationCount = '3';
            this.style.display = 'none';
        })

        const kf = {
            backgroundColor: ['antiquewhite', 'brown'],
            left: ['0px', '300px'],
            easing: [ 'ease-in', 'ease-out' ]
        }
        const am = box1.animate(kf, {
            id: 'animate',
            delay: 1000, 
            duration: 2000, 
            iterations: Infinity,
            direction: 'alternate',
            easing: 'ease-in-out'
        });

        const box2 = document.getElementById('box2');
        box2.addEventListener('transitionrun', e => {
            box2.innerText = "动画开始执行";
        })
        box2.addEventListener('transitionend', e => {
            box2.innerText = "动画执行结束";
        })
    },
    methods: {
    },
    destroyed() {

    },
};
</script>

